"use client";

import { type VariantProps } from "class-variance-authority";
import { Menu } from "lucide-react";
import { ReactNode } from "react";

import { siteConfig } from "@/config/site";
import { cn } from "@/lib/utils";

import LaunchUI from "../../logos/launch-ui";
import { Button, buttonVariants } from "../../ui/button";
import {
  Navbar as NavbarComponent,
  NavbarLeft,
  NavbarRight,
} from "../../ui/navbar";
import Navigation from "../../ui/navigation";
import { Sheet, SheetContent, SheetTrigger } from "../../ui/sheet";

// Clerk imports
import {
  SignInButton,
  SignUpButton,
  SignedIn,
  SignedOut,
  UserButton,
} from "@clerk/nextjs";

interface NavbarLink {
  text: string;
  href: string;
}

interface NavbarActionProps {
  text: string;
  href: string;
  variant?: VariantProps<typeof buttonVariants>["variant"];
  icon?: ReactNode;
  iconRight?: ReactNode;
  isButton?: boolean;
}

interface NavbarProps {
  logo?: ReactNode;
  name?: string;
  homeUrl?: string;
  mobileLinks?: NavbarLink[];
  actions?: NavbarActionProps[];
  showNavigation?: boolean;
  customNavigation?: ReactNode;
  className?: string;
}

export default function Navbar({
  logo = <LaunchUI />,
  name = "Warrantiya",
  homeUrl = siteConfig.url,
  mobileLinks = [
    { text: "Our Services", href: siteConfig.url },
    { text: "Stats", href: siteConfig.url },
    { text: "About Us", href: siteConfig.url },
  ],
  actions = [
    { text: "Sign in", href: siteConfig.url, isButton: false },
    {
      text: "Get Started",
      href: siteConfig.url,
      isButton: true,
      variant: "default",
    },
  ],
  showNavigation = true,
  customNavigation,
  className,
}: NavbarProps) {
  return (
    <header className={cn("sticky top-0 z-50 -mb-4 px-4 pb-4", className)}>
      <div className="fade-bottom bg-background/15 absolute left-0 h-24 w-full backdrop-blur-lg"></div>
      <div className="max-w-container relative mx-auto">
        <NavbarComponent>
          <NavbarLeft>
            <a
              href={homeUrl}
              className="flex items-center gap-2 text-xl font-bold"
            >
              {logo}
              {name}
            </a>
            {showNavigation && (customNavigation || <Navigation />)}
          </NavbarLeft>

          <NavbarRight>
            {/* ===================== */}
            {/*    DESKTOP SECTION    */}
            {/* ===================== */}

            <SignedOut>
              {/* Sign in (link style) */}
              <SignInButton mode="modal">
                <a className="hidden text-sm md:block cursor-pointer">
                  Sign in
                </a>
              </SignInButton>

              {/* Get Started (button style) */}
              <SignUpButton mode="modal">
                <Button
                  variant="default"
                  className="hidden md:inline-flex"
                  asChild
                >
                  <a>Get Started</a>
                </Button>
              </SignUpButton>
            </SignedOut>

            <SignedIn>
              {/* Show avatar when user is logged in */}
              <UserButton afterSignOutUrl="/" />
            </SignedIn>

            {/* ===================== */}
            {/*    MOBILE SECTION     */}
            {/* ===================== */}

            <Sheet>
              <SheetTrigger asChild>
                <Button
                  variant="ghost"
                  size="icon"
                  className="shrink-0 md:hidden"
                >
                  <Menu className="size-5" />
                  <span className="sr-only">Toggle navigation menu</span>
                </Button>
              </SheetTrigger>

              <SheetContent side="right">
                <nav className="grid gap-6 text-lg font-medium">
                  <a
                    href={homeUrl}
                    className="flex items-center gap-2 text-xl font-bold"
                  >
                    <span>{name}</span>
                  </a>

                  {mobileLinks.map((link, index) => (
                    <a
                      key={index}
                      href={link.href}
                      className="text-muted-foreground hover:text-foreground"
                    >
                      {link.text}
                    </a>
                  ))}

                  <SignedOut>
                    {/* Mobile sign in */}
                    <SignInButton mode="modal">
                      <a className="cursor-pointer text-muted-foreground hover:text-foreground">
                        Sign in
                      </a>
                    </SignInButton>

                    {/* Mobile get started */}
                    <SignUpButton mode="modal">
                      <Button className="mt-2 w-full">Get Started</Button>
                    </SignUpButton>
                  </SignedOut>

                  <SignedIn>
                    <div className="pt-4">
                      <UserButton afterSignOutUrl="/" />
                    </div>
                  </SignedIn>
                </nav>
              </SheetContent>
            </Sheet>
          </NavbarRight>
        </NavbarComponent>
      </div>
    </header>
  );
}
